import {
  iApiConfig,
  iApiRequestParams,
  iApiResourceTypeMap,
} from '../interfaces/index'

export class ApiConfig implements iApiConfig {
  _url: string
  _defaultBodyParams: any
  _defaultQueryParams: any
  _resourceTypes: any

  constructor(params) {
    this._url = params.url
    this._defaultQueryParams = params.defaultQueryParams || {}
    this._defaultBodyParams = params.defaultBodyParams || {}
    this._resourceTypes = params.resourceTypes || {}
  }

  get url(): string {
    return this._url
  }

  get defaultQueryParams(): iApiRequestParams {
    return this._defaultQueryParams
  }

  get defaultBodyParams(): iApiRequestParams {
    return this._defaultBodyParams
  }

  get resourceTypes(): iApiResourceTypeMap {
    return this._resourceTypes
  }
}

