import * as _ from 'lodash'

import {
  Observable,
  BehaviorSubject,
} from 'rxjs'

import {
  Component, OnInit, Inject, Input,
  ViewChild, OnDestroy, forwardRef,
} from '@angular/core'

import {
  MatDialogRef, MAT_DIALOG_DATA
} from '@angular/material'
import {
  createCustomElement, NgElement
} from '@angular/elements'

import {
  iDialogComponentInputProperties,
  iDialogComponent,
} from '../../interfaces/index'

import {
  ContainerDirective,
  BaseComponent
} from '../../../../declarables/index'

@Component({
  selector: 'shared-declarables-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.scss']
})
export class ModalComponent extends BaseComponent
  implements iDialogComponent {

  contentElementId: string = 'modal-content'
  actions$: BehaviorSubject<any>

  constructor(
    public dialogRef: MatDialogRef<iDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: iDialogComponentInputProperties,
  ) {
    super()
  }

  afterClosed(): Observable<any> {
    return this.dialogRef.afterClosed()
  }

  onAction(event) {
    if(event.action.name == 'cancel') {
      this.dialogRef.close(event.action.name)
    }
    else {
      this.data.actions$.next(event)
    }
  }

  onClose(event) {
    this.dialogRef.close('close')
  }
}
