import { Component, OnInit } from '@angular/core'

import {
  Mixin,
} from '../../../providers/decorators/index'

import {
  DataInspector,
} from '../../../providers/mixins/index'

@Component({
  selector: 'shared-base',
  template: '',
  styleUrls: ['./base.component.scss']
})
@Mixin([DataInspector])
export class BaseComponent implements OnInit {
  id: string;
  
  constructor() {
    this.log()
  }

  ngOnInit() {}

  log(isEnabled: boolean = false) {
    if(isEnabled) {
      let date = new Date()
      this.id = date.toISOString()
      let message = "The id of the " + this.constructor.name + " component is " + this.id
      console.log(message)
    }
  }

  //Mixin properties
  inspectData(...args: any[]) {}
}
