import * as _ from 'lodash'

import {
  Observable, BehaviorSubject
} from 'rxjs'

import {
  Component, Input, Output,
  EventEmitter
} from '@angular/core'

import { FormGroup }        from '@angular/forms'

import {
  InputType,
  iInputGroup,
  InputControlService,
} from '../../../providers/index'

import { BaseComponent } from '../base/base.component'

@Component({
  selector: 'shared-declarables-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss'],
  providers: [InputControlService]
})
export class FormComponent extends BaseComponent {
  @Input()
  inputGroup$: Observable<iInputGroup>

  @Input()
  dataService: any

  @Input()
  entity: any 

  @Input()
  defaultParams: any 

  inputGroup: iInputGroup
  _formGroup: FormGroup

  constructor(
    private inputControlService: InputControlService
  ) {
    super()
  }

  ngOnInit() {
    this.handleActions()

    this.inputGroup$
      .subscribe(inputGroup => {
        this.inputGroup = inputGroup
        this._formGroup = this.inputControlService.toFormGroup(inputGroup)
      })
  }

  handleActions() {
    if(this.dataService && this.entity) {
      this.dataService.handleDialogActions(this)
    }
  }

  get formGroup() {
    return this._formGroup
  }

  payload() {
    let payload = this.formGroup.value
    if(this.defaultParams) {
      return _.merge(payload, this.defaultParams)
    }
    else {
      return payload
    }
  }
}
