import {
  Component,
  Inject,
} from '@angular/core'

import {
  BehaviorSubject,
} from 'rxjs'

import {
  MatDialogRef,
  MAT_DIALOG_DATA,
} from '@angular/material'

import {
  iDialogAction,
  iDialogComponentInputProperties,
  iDialogComponent,
} from '../../interfaces/index'

import { BaseComponent } from '../../../../declarables/index'

@Component({
  selector: 'ceo-dialog',
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.scss']
})
export class DialogComponent extends BaseComponent {
  contentElementId: string = 'dialog-content'

  constructor(
    public dialogRef: MatDialogRef<iDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: iDialogComponentInputProperties,
  ) {
    super()
  }

  ngOnInit() {
    super.ngOnInit()
    this.data.actions$.subscribe(action => this.onAction(action))
  }

  onClose(event) {
    this.dialogRef.close('close')
    this.emitAction(this.buildAction('close'))
  }


  onAction(action: iDialogAction) {
    //console.log("We have the dialog action " + action.name)
  }

  private buildAction(name: string, payload: any = null) {
    return {
      name: name,
      payload: payload
    }
  }

  emitAction(action: iDialogAction) {
    this.data.actions$.next(action)
  }
}
