import { Injectable } from '@angular/core';

import {
  FormMemberType,
  iFactory,
  iFormControl,
  iFormItem,
  iFormItemProps,
  iFormMemberFactoryParams,
  iLabelElement,
} from '../interfaces/index'

import {
  FormItem,
  LabelElement,
} from '../classes/index'

import { FormControlFactory } from './form-control-factory.service'

@Injectable({
  providedIn: 'root'
})
export class FormItemFactory
  implements iFactory<iFormItem, iFormMemberFactoryParams> {

  type: FormMemberType = 'form-item'

  constructor(
    private formControlFactory: FormControlFactory,
  ) { }

  build(params: iFormMemberFactoryParams): iFormItem {
    let label = this.buildLabel(params)
    let control = this.buildControl(params)
    return this.buildItem(label, control)
  }

  private buildItem(label: iLabelElement, control: iFormControl): iFormItem {
    let itemParams = {
      label: label,
      control: control
    }
    return new FormItem(itemParams)
  }

  buildLabel(params: iFormMemberFactoryParams): iLabelElement {
    let props = <iFormItemProps>params.data
    return new LabelElement(props.label)
  }

  buildControl(params: iFormMemberFactoryParams): iFormControl {
    let props = <iFormItemProps>params.data
    return this.formControlFactory.build(props.control)
  }
}
