import {
  AbstractControl as NgAbstractControl,
} from '@angular/forms'

import {
  FormMemberType,
  iFormMember,
} from '../interfaces/index'

export class FormMember implements iFormMember {
  protected _ngControl: NgAbstractControl
  type: FormMemberType

  constructor(props: any = {}) {
    Object.assign(this, props)
  }

  get ngControl(): NgAbstractControl {
    return this._ngControl
  }

  get value(): any {
    return this.ngControl.value
  }

  get valid(): boolean {
    return this.ngControl.valid
  }

  markAsTouchedAndDirty(): void {
    this.markAsTouched()
    this.markAsDirty()
  }


  markAsTouched() {
    this.ngControl.markAsTouched()
  }


  markAsDirty() {
    this.ngControl.markAsDirty()
  }
}
