import * as _ from 'lodash'

import { Injectable } from '@angular/core'

import {
  iFactory,
  iFormMember,
  iFormMemberFactoryParams,
} from '../interfaces/index'

import { FormItemFactory }   from './form-item-factory.service'
import { FormGroupFactory }  from './form-group-factory.service'

interface iFormMemberFactory extends iFactory<iFormMember, iFormMemberFactoryParams> {}

@Injectable({
  providedIn: 'root'
})
export class FormMemberFactory
  implements iFactory<iFormMember, iFormMemberFactoryParams> {

  private factories: iFactory<iFormMember, iFormMemberFactoryParams>[]
  private defaultMemberFactory: iFactory<iFormMember, iFormMemberFactoryParams>

  constructor(
    private formItemFactory: FormItemFactory,
    private formGroupFactory: FormGroupFactory,
  ) {
    this.factories = [
      formItemFactory,
      formGroupFactory
    ]
    this.defaultMemberFactory = formItemFactory
  }

  build(params: iFormMemberFactoryParams): iFormMember {
    let factory = this.resolveMemberFactory(params)
    return factory.build(params)
  }

  private resolveMemberFactory(
    params: iFormMemberFactoryParams
  ): iFactory<iFormMember, iFormMemberFactoryParams> {

    let typeFactory = _.find(this.factories, { type: params.type })

    return <iFormMemberFactory>_.defaultTo(typeFactory, this.defaultMemberFactory)
  }
}
