import { Injectable } from '@angular/core'

import { Observable } from 'rxjs'

import {
  HttpResponse, HttpRequest
} from '@angular/common/http'


import {
  iRequestCacheService,
  iRequestCacheEntry,
} from '../../interfaces/index'

export type CacheEntryResponse =
  HttpResponse<any> | undefined

@Injectable()
export class RequestCacheService implements iRequestCacheService {
  private cacheEntries: iRequestCacheEntry[] = []

  get(req: HttpRequest<any>): CacheEntryResponse {
    return undefined
  }

  put(req: HttpRequest<any>, response: HttpResponse<any>): void {
    let cacheKey = 1
    let cacheEntry = {
      url: '',
      response: response,
      lastRead: 1,
    }
    this.cacheEntries[cacheKey] = cacheEntry
  }
}
