import {
  Component, Input,
} from '@angular/core'

import {
  BaseComponent,
} from '../../../../declarables/index'

import {
  iFormItem,
} from '../../interfaces/index'

@Component({
  selector: 'ceo-forms-form-item',
  templateUrl: './form-item.component.html',
  styleUrls: ['./form-item.component.scss']
})
export class FormItemComponent extends BaseComponent {
  @Input() key: string
  @Input() item: iFormItem

  classes: any = {
    valid: 'is-valid',
    invalid: 'is-invalid',
  }

  formGroupClass() {
    let classes = []
    let ngControl = this.item.ngControl

    if(ngControl.valid) {
      classes.push(this.classes.valid)
    }
    if(ngControl.invalid) {
      classes.push(this.classes.invalid)
    }

    return classes
  }
}
