import * as _ from 'lodash'

import {
  Injectable, Inject,
} from '@angular/core'

import {
  ApiUrl,
  iApiConfig,
  iApiUrlProvider,
  iApiQueryParams,
  iApiResourceIdentifier,
  iApiResourceTypeConfig,
} from '../../../interfaces/index'

import {
  API_CONFIG,
} from '../../../tokens/index'

@Injectable()
export class RequestUrlBuilder {
  constructor(
    @Inject(API_CONFIG) public apiConfig,
  ) { }

  build(ri: iApiResourceIdentifier): ApiUrl {
    return this.getResourceTypeUrl(ri)
  }

  protected getResourceTypeUrl(ri: iApiResourceIdentifier): ApiUrl {
    let fragments = [
      this.apiConfig.url,
      this.getResourceTypeUrlFragment(ri),
    ]

    if(this.isSingleResourceRequest(ri)) {
      let singleRequestFragment = this.singleResourceRequestFragment(ri)
      fragments.push(singleRequestFragment)
    }

    return _.join(fragments, '/')
  }

  protected getResourceTypeUrlFragment(ri: iApiResourceIdentifier): any {
    let resourceType = this.getResourceType(ri)
    let resourceConfig = resourceType.config
    return resourceConfig.urlFragment(ri)
  }

  protected getResourceType(
    ri: iApiResourceIdentifier,
  ): iApiResourceTypeConfig {

    let ofType = (resourceType: iApiResourceTypeConfig) => {
      return resourceType.config.hasResourceType(ri)
    }

    return _.find(this.apiConfig.resourceTypes, ofType)
  }

  protected isSingleResourceRequest(ri: iApiResourceIdentifier): boolean {
    return !_.isNil(ri.id)
  }

  protected singleResourceRequestFragment(ri: iApiResourceIdentifier): string {
    return ri.id
  }
}
