import * as _ from 'lodash'

import { Injectable } from '@angular/core'
import {
  FormControl, FormGroup,
  Validators, AbstractControl,
} from '@angular/forms'

import {
  iInputGroup,
} from '../interfaces/index'

export interface iAbstractControlMap {
  [key: string]: AbstractControl
}

export class InputControlService {
  toFormGroup(inputGroup: iInputGroup) {
    return this.buildFormGroup(inputGroup)
  }

  private buildFormGroup(inputGroup): FormGroup {
    return new FormGroup(this.buildControls(inputGroup))
  }

  private buildControls(inputGroup: iInputGroup): iAbstractControlMap {
    return _.reduce(
      inputGroup.inputs,
      _.bind(_.partial(this.buildAbstractControl, inputGroup), this),
      {}
    )
  }

  private buildAbstractControl(inputGroup, controls, input) {
    var build = this.getFormControlBuilder(input)
    input.key = this.generateInputKey(input, inputGroup)
    controls[input.key] = build(input)
    return controls
  }

  private getFormControlBuilder(input) {
    if(input.constructor.name == 'InputGroup') {
      return _.bind(this.buildFormGroup, this)
    }
    else {
      return _.bind(this.buildFormControl, this)
    }
  }

  private generateInputKey(input, inputGroup) {
    return input.key
  }


  private buildFormControl(inputControl) {
    let value = inputControl.value || ''
    return new FormControl(
      value,
      Validators.compose(inputControl.validators)
    )
  }
}
