import * as _ from 'lodash'

import {
  Component, OnInit, Input,
  EventEmitter,
  Output,
} from '@angular/core'

import { BaseComponent } from '../base/base.component'

import {
  iNavigationMenu,
} from '../../../providers/interfaces'

@Component({
  selector: 'ceo-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.scss']
})
export class NavigationComponent extends BaseComponent {
  @Input()
  navigationMenu: iNavigationMenu

  @Input()
  navigationLinks: any[]

  @Input()
  menuClass: string = 'horizontal'

  @Output()
  itemEvent: EventEmitter<any> = new EventEmitter<any>()

  getMenuClass(){
    let classNames = ['menu', this.menuClass]
    return _.join(classNames, ' ')
  }


  onItemEvent(event) {
    this.itemEvent.emit(event)
  }
}
