import * as _ from 'lodash'

import {
  Component, Input,
  EventEmitter,
  Output,
} from '@angular/core'

import { BaseComponent } from '../../base/base.component'

import {
  iNavigationMenuItem,
  iNavigationMenu,
} from '../../../../providers/interfaces/index'

@Component({
  selector: 'ceo-navigation-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.scss']
})
export class NavComponent extends BaseComponent {
  @Input()
  navigationMenu: iNavigationMenu

  @Output()
  itemEvent: EventEmitter<any> = new EventEmitter<any>()

  onItemEvent(event) {
    this.itemEvent.emit(event)
  }
}
