import * as _ from 'lodash'

import {
  iBreadcrumb,
  iBreadcrumbItem,
} from '../interfaces/index'

export class Breadcrumb implements iBreadcrumb {
  public length: number

  constructor(
    public items: iBreadcrumbItem[] = []
  ) {
    this.length = items.length
  }


  // Iterator
  // Allows us to use the collections in angular directives
  // (i.e. ngFor, etc)
  [Symbol.iterator]() {
    let current = 0
    let items = this.items
    return  {
      next: function () {
        let noItems = _.isEmpty(items)
        let value = noItems ? null : items[current++]
        let done = noItems ? true : current > items.length
        return {
          value: value,
          done: done
        }
      }
    }
  }
}
