import * as _ from 'lodash'

import {
  Observable,
  of as observableOf,
  throwError as observableThrowError,
  pipe,
} from 'rxjs'
import {
  map, mergeMap,
  catchError,
} from 'rxjs/operators'

import { Injectable } from '@angular/core'
import { Response }   from '@angular/http'

import {
  HttpClient, HttpParams,
  HttpHeaders,
  HttpResponse,
} from '@angular/common/http'

import {
  ApiUrl,
  iApiResourceIdentifier,
  iApiResponse,
  iApiService,
  iApiErrorResponse,
  iApiRequestPayload,
} from '../../../interfaces/index'

import { CsvToJsonService }         from '../../csv-to-json.service'
import { RequestUrlBuilder }     from './request-url-builder.service'
import { RequestOptionsBuilder } from './request-options-builder.service'
import { ResponseParser }           from './response-parser.service'

@Injectable()
export class ApiService implements iApiService {
  constructor(
    private http: HttpClient,
    private csvToJsonService: CsvToJsonService,
    private responseParser: ResponseParser,
    private urlBuilder: RequestUrlBuilder,
    private optionsBuilder: RequestOptionsBuilder,
  ) {}

  post$(payload: iApiRequestPayload): Observable<any> {
    return this.handleRequest$('post', payload)
  }

  delete$(payload: iApiRequestPayload): Observable<any> {
    return this.handleRequest$('delete', payload)
  }

  get$(payload: iApiRequestPayload): Observable<any> {
    return this.handleRequest$('get', payload)
  }

  put$(payload: iApiRequestPayload): Observable<any> {
    return this.handleRequest$('put', payload)
  }

  // Aliases
  update$(payload: iApiRequestPayload): Observable<any> {
    return this.put$(payload)
  }

  create$(payload: iApiRequestPayload): Observable<any> {
    return this.post$(payload)
  }

  // Private methods
  private handleRequest$(method, payload) {
    return this.executeRequest$(method, payload).pipe(
      catchError((error: iApiErrorResponse) => {
        return this.handleError$(error, payload)
      }),
      mergeMap((response: HttpResponse<any>) => {
        return this.processedResponse$(response, payload.resourceIdentifier)
      }),
    )
  }

  private handleError$(
    error: iApiErrorResponse,
    payload: iApiRequestPayload
  ): Observable<any> {
    console.error("Error in API Service: " + error)
    return observableThrowError(error)
  }

  private executeRequest$(
    method: string,
    payload: iApiRequestPayload
  ) {
    let url = this.getUrl(payload.resourceIdentifier)
    let httpOpts = this.getHttpOpts(method, url, payload)

    return this.http[method](url, httpOpts)
  }

  private getUrl(ri: iApiResourceIdentifier) {
    return this.urlBuilder.build(ri)
  }

  private getHttpOpts(
    method: string,
    url: ApiUrl,
    payload: iApiRequestPayload
  ) {
    return this.optionsBuilder.build(method, url, payload)
  }

  private processedResponse$(
    apiResponse: HttpResponse<any>,
    ri: iApiResourceIdentifier
  ) {

    return this.jsonData$(apiResponse.body).pipe(
      map(json => this.sanitizedData(json, ri)),
      map(json => this.buildApiResponse(json, ri)),
    )
  }

  private jsonData$(data): Observable<any> {
    if(_.isString(data)) {
      return this.csvToJsonData$(data)
    }
    else {
      return observableOf(data)
    }
  }

  // Remove empty keys and attributes
  private sanitizedData(data, ri) {
    var isSimpleVariable = (value) => {
      return (_.isNumber(value) || _.isString(value))
    }

    var isPresent = (obj) => {
      return obj !== '' && obj !== null
    }

    let sanitize = (object) => {
      // Removed this pickBy call -- null and empty string
      // values are specified by backend and should
      // be preserved; might this cause issues elsewhere?
      // They should be fixed in the consumer.
      //var filteredObject = _.pickBy(object, isPresent)
      var filteredObject = object

      let finalFilter = (obj) => {
        if(obj !== filteredObject && _.isPlainObject(obj)) {
          return sanitize(obj)
        }
        else if(_.isArray(obj)) {
          if(_.isEmpty(obj) || isSimpleVariable(obj[0])) {
            return obj
          }
          return _.reject(_.map(obj, sanitize), _.isEmpty)
        }

        return undefined
      }

      return _.cloneDeepWith(filteredObject, finalFilter)
    }

    var sanitizedData
    if(_.isArray(data)) {
      sanitizedData = _.map(data, sanitize)
    }
    else {
      sanitizedData = sanitize(data)
    }
    return sanitizedData
  }

  private buildApiResponse(data, ri: iApiResourceIdentifier): iApiResponse {
    return {
      data: this.responseParser.parse(data),
      resourceIdentifier: ri,
    }
  }

  private csvToJsonData$(data): Observable<any> {
    let observable = Observable.create((observer) => {
      var jsonData = []
      var jsonApiResponse = {
        data: jsonData,
        included: [],
      }

      let onNext = (json, index) => {
        let isValid = (propValue, propName) => {
          return !_.isNil(propValue)
        }
        let sanitizedJson = _.pickBy(json, isValid)


        jsonData.push(json)
      }
      let onError = (error) => error
      let onComplete = () => {
        return observer.next(jsonApiResponse)
      }

      let converter = this.csvToJsonService.csvToJsonFromString(data)
        .subscribe(onNext, onError, onComplete)

    })

    return observable
  }
}

