import * as _ from 'lodash'

import { Injectable } from '@angular/core'

import {
  FormControl as NgFormControl,
  FormGroup as NgFormGroup,
} from '@angular/forms'

import {
  FormMemberType,
  iFormWrapper,
  iFormMemberMap,
  iFormGroupProps,
} from '../interfaces/index'

import {
  FormWrapper,
} from '../classes/index'

import { FormGroupFactory } from './form-group-factory.service'

@Injectable({
  providedIn: 'root'
})
export class FormFactory {
  constructor(
    private formGroupFactory: FormGroupFactory
  ) { }

  build(members: iFormMemberMap): iFormWrapper {
    let data = {
      members: members
    }
    let params = {
      type: 'form' as FormMemberType,
      data: data as iFormGroupProps
    }
    return this.formGroupFactory.build(params)
  }
}
