// Similar implementation to:
// https://angular.io/guide/dynamic-form

import * as _ from 'lodash'

import {
  BaseFormControl,
} from './base.form-control'

import {
  FormControlType,
  InputTypeAttribute,
  iInputFormControl,
} from '../../interfaces/index'

let inputFormControlDefaults = {
  key: '',
  label: '',
  placeholder: '',
  row: null,
  value: null,
  inputType: 'text' as InputTypeAttribute,
  required: false,
  options: [],
}

export class InputFormControl extends BaseFormControl
  implements iInputFormControl {

  private defaults: Partial<iInputFormControl> = inputFormControlDefaults
  controlType: FormControlType = 'input'

  inputType: InputTypeAttribute
  required: boolean
  options: { text: string, value: string }[] = []

  constructor(init: Partial<iInputFormControl> = {}) {
    super(init)

    init = _.defaults(init, this.defaults)
    Object.assign(this, init)
  }
}
