import { Injectable } from '@angular/core'

import {
  FormControl as NgFormControl,
  Validators as NgValidators,
} from '@angular/forms'

import {
  iFormControl,
} from '../../interfaces/index'

@Injectable({
  providedIn: 'root'
})
export class FormControlFactory {
  build(formControl: iFormControl): NgFormControl {
    return new NgFormControl(
      formControl.value,
      NgValidators.compose(formControl.validators),
    )
  }
}

