// Service to create custom elements.

import * as _ from 'lodash'

import {
  BehaviorSubject,
} from 'rxjs'

import {
  Injectable, Inject,
} from '@angular/core'

import {
  CustomElementManager,
} from '../../../providers/index'

import {
  DialogComponent
} from '../components/dialog/dialog.component'

import {
  iDialogComponent,
  iDialogConfig,
  iDialogsConfig,
  iDialogContentComponent,
  iDialogHtmlElement,
} from '../interfaces/index'

import {
  DIALOGS_CONFIG,
} from '../tokens/index'

@Injectable({
  providedIn: 'root'
})
export class ConfigService {
  constructor(
    private customElementManager: CustomElementManager,
    @Inject(DIALOGS_CONFIG) public dialogsConfig,
  ) {}

  provide(
    dialogElement: iDialogHtmlElement,
    config: iDialogConfig | string,
  ): iDialogConfig {

    let component = this.getComponent(dialogElement)

    config = this.getDialogConfig(dialogElement, config)

    let configActions = this.dialogConfigActions(component)

    return _.defaultsDeep(config, configActions, this.defaults)
  }

  get dialogs() {
    return _.reduce(_.map(this.dialogsConfig, 'dialogs'), _.merge)
  }

  get defaults() {
    let defaults = this.defaultConfig.defaults
    let others = {
      componentType: DialogComponent,
    }
    return _.merge(defaults, others)
  }

  get defaultConfig() {
    return _.find(this.dialogsConfig, { isDefault: true }) as iDialogsConfig
  }

  private getDialogConfig(
    dialogElement,
    config: iDialogConfig | string
  ) {

    var dialogName
    if(this.customElementManager.isCustomElement(dialogElement)) {
      dialogName = dialogElement.localName
    }
    if(_.isString(config)) {
      dialogName = config
    }

    if(dialogName) {
      return _.get(this.dialogs, dialogName, this.defaults)
    }
    else {
      return config
    }
  }

  private dialogConfigActions(component: iDialogContentComponent) {
    if(component.dialogActions$) {
      return {
        actions$: component.dialogActions$
      }
    }
    return {}
  }

  private getComponent(element: iDialogHtmlElement): iDialogContentComponent {
    let component = element

    if(this.customElementManager.isCustomElement(element)) {
      component = this.customElementManager.getNgComponent(element)
    }

    return component as iDialogContentComponent
  }
}
