import { 
  Observable,
} from 'rxjs'

import { filter } from 'rxjs/operators'

import {
  Directive,
  HostListener,
  OnInit,
} from "@angular/core"

import {
  Router,
  NavigationEnd,
} from '@angular/router'


@Directive({
  selector: "[ceoScrollTop]"
})
export class ScrollTopDirective implements OnInit {
  private _routerNavigationEnd$: Observable<NavigationEnd>
  
  constructor(
    private router: Router,
  ) {}
  
  ngOnInit() {
    this.routerNavigationEnd$
      .subscribe(event => this.scrollTop())

    this.scrollTop()
  }

  get routerNavigationEnd$(): Observable<NavigationEnd> {
    if(!this._routerNavigationEnd$) {
      this._routerNavigationEnd$ = this.buildNavEndEvent()
    }

    return this._routerNavigationEnd$
  }

  private buildNavEndEvent(): Observable<NavigationEnd> {
    return this.router.events.pipe(
      filter(event => event instanceof NavigationEnd)
    ) as Observable<NavigationEnd>
  }
  
  scrollTop() {
    console.log("scroll to the top -- directive")
    window.scrollTo(0, 0)
  }
}

