// Loads the pane list
// Initializes it to the Pane class
// Serves as Pane Factory;
// uses pane attributes to build Pane objects

import * as _ from 'lodash'

import {
  zip as observableZip,  
  Observable, of, BehaviorSubject,
} from 'rxjs';
import {
  map,
  startWith,
} from 'rxjs/operators'

import { Injectable } from '@angular/core';

import { iPaneProvider, iPane } from '../interfaces/index'

import { PaneFactory }     from './pane-factory.service'

@Injectable({
  providedIn: 'root'
})
export class PaneProvider implements iPaneProvider {
  _panes$: Observable<iPane[]>;
  _activePane$: BehaviorSubject<any> = new BehaviorSubject<any>(null)

  private panes: iPane[] = [];

  constructor(
    private paneFactory: PaneFactory
  ) {
    this._panes$ = this.paneFactory.build$()

    this._panes$.subscribe(panes => this.setPanes(panes))
  }

  get panes$(): Observable<iPane[]> {
    return this._panes$
  }

  get activePane$(): Observable<iPane> {
    return this._activePane$.pipe(
      startWith(this.activePaneFromPanes(this.panes))
    )
  }

  setPaneActiveStatus(pane, activeStatus): boolean {
    pane.setStatus(activeStatus)
    if(activeStatus) {
      this.emitActivePane(pane)
    }
    return true
  }

  private emitActivePanes(panes) {
    let pane = this.activePaneFromPanes(panes)
    this.emitActivePane(pane)
  }

  private emitActivePane(pane) {
    this._activePane$.next(pane) 
  }

  private setPanes(panes) {
    this.panes = panes
    this.emitActivePanes(this.panes)
  }

  private activePaneFromPanes(panes: iPane[]): iPane {
    return _.find(panes, 'active')
  }
}


