import * as _ from 'lodash'

import {
  FormControl as NgFormControl,
  AbstractControl as NgAbstractControl,
} from '@angular/forms'

import {
  words,
} from '@ceo/core'

import {
  iFormControl,
  iFormMemberError,
  FormControlType,
} from '../../interfaces/index'

export class BaseFormControl implements iFormControl {
  controlType: FormControlType
  protected _ngControl: NgAbstractControl

  displayName: string
  elementId: string
  key: string
  label: string
  order: number = -1
  placeholder: string
  row: number
  text: string
  validators: any[] = []
  value: any

  constructor(init: Partial<iFormControl> = {}) {
    Object.assign(this, init)
  }

  get ngControl(): NgAbstractControl {
    return this._ngControl
  }

  set ngControl(ngControl: NgAbstractControl) {
    this._ngControl = ngControl
  }

  get showValidations(): boolean {
    return this.ngControl.errors &&
      (this.ngControl.dirty || this.ngControl.touched)
  }

  get errorMessages(): iFormMemberError[] {
    let buildErrorMessage = (value, key) => {
      var message = ''

      let lowerCased = _.lowerCase(this.displayName)
      let firstLetter = lowerCased[0]
      let startsWithVowel = _.includes(['a', 'e', 'i', 'o', 'u'], firstLetter)
      let article = startsWithVowel ? 'an' : 'a'
    
      if(key == 'required') {
        message = `Please enter ${article} ${lowerCased}.`
      }

      if(key == 'email') {
        message = `Please enter a valid email.`
      }

      return {
        key: key,
        message: message,
      }
    }
    return _.map(this.ngControl.errors, buildErrorMessage)
  }

  get name(): string {
    return this.displayName
  }
}
