import * as _  from 'lodash';

import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'customSearch'
})
export class CustomSearchPipe implements PipeTransform {
  transform(collection: any, args?: any): any {
    if (!_.isEmpty(args) && collection) {
      return this.filter(collection, args);
    }

    return collection;
  }

  filter(collection, attributes) {
    let filterCollection = (value, key) => {
      if(_.isEmpty(value)) {
        return collection.entities
      }

      return _.reduce(collection.entities, function(entities, entity) {
        if(_.includes(_.lowerCase(entity[key]),
          _.lowerCase(value)) &&
          !_.isEmpty(value)) {
          entities.push(entity);
          return entities;
        }
        return entities;
      }, [])
    }

    return _.uniqBy(
      _.flatten(_.map(attributes, filterCollection)),
      'id');
  }
}
