import * as _ from 'lodash'

import {
  Component, Input,
  EventEmitter,
  Output,
} from '@angular/core'

import { BaseComponent } from '../../base/base.component'

import {
  iNavigationMenuItem,
} from '../../../../providers/interfaces/index'

@Component({
  selector: 'ceo-dropdown-item',
  templateUrl: './item.component.html',
  styleUrls: ['./item.component.scss']
})
export class ItemComponent extends BaseComponent {
  @Input()
  item: iNavigationMenuItem

  @Output()
  itemEvent: EventEmitter<any> = new EventEmitter<any>()

  displayType() {
    return this.item.displayType
  }

  className(item) {
    let classes = [item.className]

    if(item.hasSubmenu()) {
      classes.push('dropdown-column')
    }

    return _.join(classes, ' ')
  }

  onItemEvent(event) {
    this.itemEvent.emit(event)
  }
}
