import * as _ from 'lodash'

import { Injectable } from '@angular/core'

import {
  DialogComponentType,
  iDialogComponentConfig,
  iDialogConfig,
  iDialogContentComponent,
  iDialogComponent,
  iDialogHtmlElement,
  iDialogService,
  iMatDialogConfig,
} from '../interfaces/index'

import {
  MatDialogAdapterService,
} from './mat-dialog-adapter.service'

import {
  ConfigService,
} from './config.service'

@Injectable({
  providedIn: 'root'
})
export class DialogService implements iDialogService {
  constructor(
    private dialogAdapter: MatDialogAdapterService,
    private configService: ConfigService
  ) {}

  open(
    dialogElement: iDialogHtmlElement,
    config: iDialogConfig | string,
  ): iDialogComponent {

    config = this.buildDialogConfig(dialogElement, config)

    return this.openVendorDialog(dialogElement, config)
  }

  closeAll() {
    this.dialogAdapter.closeAll()
  }

  private buildDialogConfig(
    dialogElement: iDialogHtmlElement,
    config: iDialogConfig | string,
  ): iDialogConfig {
    return this.configService.provide(dialogElement, config)
  }

  private openVendorDialog(
    dialogElement: iDialogHtmlElement,
    config: iDialogConfig,
  ): iDialogComponent {
    return this.dialogAdapter.open(dialogElement, config)
  }

}
