import * as _ from 'lodash'

import { Injectable } from '@angular/core'

import {
  FormControl as NgFormControl,
} from '@angular/forms'

import {
  iFactory,
  iFormControl,
  iFormControlProps,
  iFormControlConstructor,
} from '../interfaces/index'

import {
  CheckboxFormControl,
  SelectFormControl,
  InputFormControl,
  TextboxFormControl,
} from '../classes/index'

import { NgFormControlFactory } from './ng-forms/index'

@Injectable({
  providedIn: 'root'
})
export class FormControlFactory
  implements iFactory<iFormControl, any> {

  constructor(
    private ngFormControlFactory: NgFormControlFactory,
  ) { }

  build(props: iFormControlProps): iFormControl {
    let formControl = this.buildFormControl(props)
    formControl.ngControl = this.buildNgFormControl(formControl)
    return formControl
  }

  private buildFormControl(props: iFormControlProps): iFormControl {
    let controlConstructor = this.resolveControlConstructor(props)
    return new controlConstructor(props)
  }

  private resolveControlConstructor(
    props: iFormControlProps,
  ): iFormControlConstructor {

    switch(props.controlType) {
      case "input": {
        return InputFormControl
      }
      case "textbox": {
        return TextboxFormControl
      }
      case "select": {
        return SelectFormControl
      }
      case "checkbox": {
        return CheckboxFormControl
      }
      default: {
        return InputFormControl
      }
    }
  }

  private buildNgFormControl(formControl: iFormControl): NgFormControl {
    return this.ngFormControlFactory.build(formControl)
  }
}
