import * as _ from 'lodash'

import { Injectable } from '@angular/core'

import {
  FormGroup as NgFormGroup,
  AbstractControl as NgAbstractControl,
} from '@angular/forms'

import {
  iMap,
} from '../../../../providers/index'

import {
  iFormGroup,
  iFormMember,
  iNgFormMemberFactory,
} from '../../interfaces/index'

import { FormControlFactory } from './form-control-factory.service'

@Injectable({
  providedIn: 'root'
})
export class FormGroupFactory {
  constructor(
    private formControlFactory: FormControlFactory
  ) {}

  build(formGroup: iFormGroup): NgFormGroup {
    return this.buildNgFormGroup(formGroup)
  }

  private buildNgFormGroup(formGroup: iFormGroup): NgFormGroup {
    let ngControls = this.buildNgControls(formGroup.members)
    return new NgFormGroup(ngControls)
  }

  private buildNgControls(members: iMap<iFormMember>): iMap<NgAbstractControl> {
    let getNgControl = (member: iFormMember) => {
      return this.getNgControl(member)
    }

    return _.mapValues(members, getNgControl)
  }

  private getNgControl(member: iFormMember): NgAbstractControl {
    if(!member.ngControl) {
      let control = this.buildNgControl(member)
      member.ngControl = control
    }

    return member.ngControl
  }

  private buildNgControl(member: iFormMember): NgAbstractControl {
    let factory = <iNgFormMemberFactory>this.resolveControlFactory(member)
    let ngControl = factory.build(member)
    return ngControl
  }

  private resolveControlFactory(member: iFormMember) {
    switch(member.type) {
      case "form-item": {
        return this.formControlFactory
      }
      case "form-group": {
        return this
      }
      default: {
        return this.formControlFactory
      }
    }
  }
}
