// Service to manage panes depending on the page.
// 

import {
  Observable,
} from 'rxjs';

import { first } from 'rxjs/operators';

import * as _ from 'lodash'

import { Injectable } from '@angular/core'

import { PaneProvider } from './pane-provider.service'

import { iPane } from '../interfaces/index'

@Injectable({
  providedIn: 'root'
})
export class PaneManager {
  constructor(
    private paneProvider: PaneProvider
  ) {}

  get panes$(): Observable<iPane[]> {
    return this.paneProvider.panes$
  }

  get activePane$(): Observable<iPane> {
    return this.paneProvider.activePane$
  }

  activatePane(pane: iPane): void {
    let activePane$ = this.activePane$.pipe(
      first()
    )
    activePane$.subscribe(activePane => {
      this.setPaneActiveStatus(activePane, false)
      this.setPaneActiveStatus(pane, true)
    })
  }

  private setPaneActiveStatus(
    pane: iPane = null,
    activeStatus: boolean = false): boolean {

    if(!pane) {
      return false
    }

    return this.paneProvider.setPaneActiveStatus(pane, activeStatus)
  }

}
