import * as _ from 'lodash'

import { Observable } from 'rxjs'

import {
  Component, OnInit, Input,
  ViewChild, ComponentFactoryResolver, forwardRef,
} from '@angular/core'

import { ContainerDirective } from '../../directives/index'
import { iPane }              from '../../../providers/interfaces/index'

import { BaseComponent } from '../base/base.component'

@Component({
  selector: 'ceo-shared-declarables-pane',
  templateUrl: './pane.component.html',
  styleUrls: ['./pane.component.scss']
})
export class PaneComponent extends BaseComponent {
  @ViewChild(forwardRef(() => ContainerDirective))
  private containerDirective: ContainerDirective
  componentRef: any
  @Input()
  pane$: Observable<iPane>

  @Input()
  inputs?: any[]

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
  ) {
    super()
  }

  ngOnInit() {
    this.launch()
  }

  private launch() {
    this.pane$.subscribe(pane => {
      this.loadComponent(pane)
    })
  }

  private loadComponent(pane: iPane): void {
    let component = this.createComponent(pane.componentFactory)
  }

  private resolveComponentFactory(component) {
    return this.componentFactoryResolver
      .resolveComponentFactory(component)
  }

  private createComponent(componentFactory) {
    let viewContainerRef = this.containerDirective.viewContainerRef
    viewContainerRef.clear()
    this.componentRef = viewContainerRef.createComponent(componentFactory)
    this.setInputs()
    return this.componentRef
  }

  private setInputs() {
    if(this.inputs){
      let setInput = (value, prop) => {
        this.componentRef.instance[prop]  = value
      }
      _.forEach(this.inputs, setInput)
    }
  }
}
