import * as _ from 'lodash'

import {
  Injectable,
  Inject,
} from '@angular/core'

import { HttpParams } from '@angular/common/http'
import { HttpParamsOptions } from '@angular/common/http/src/params'

import {
  jsonToQueryParamsObject,
} from '@ceo/core'


import {
  ApiUrl,
  iApiConfig,
  iApiQueryParams,
  iApiRequestParams,
  iApiResourceIdentifier,
  iApiResourceTypeConfig,
  iApiRequestOptions,
  iApiRequestPayload,
} from '../../../interfaces/index'

import {
  API_CONFIG,
} from '../../../tokens/index'

@Injectable()
export class RequestOptionsBuilder {
  constructor(
    @Inject(API_CONFIG) public apiConfig,
  ) { }

  build(
    method: string,
    url: ApiUrl,
    payload: iApiRequestPayload
  ) {
    if(this.isBodyRequest(method)) {
      return this.getBody(payload.resourceIdentifier.data)
    }
    else {
      return this.getOptions(url, payload.resourceIdentifier)
    }
  }

  getBody(data: any): any {
    let body = this.sanitizedParams(data)
    body = _.merge({}, body, this.apiConfig.defaultQueryParams)

    return {
      body: body,
      observe: 'response',
    }
  }

  getOptions(url: ApiUrl, ri: iApiResourceIdentifier): iApiRequestOptions {
    return {
      params: this.getParams(ri),
      responseType: this.getResponseType(url),
      observe: 'response',
    }
  }
  

  getParams(ri: iApiResourceIdentifier): any {
    let params =
      _.merge({}, this.apiConfig.defaultQueryParams, this.filterParams(ri))

    return jsonToQueryParamsObject(params)
  }

  getResponseType(url): string {
    if(this.isTextResponseType(url)) {
      return 'text'
    }
    else {
      return 'json'
    }
  }

  protected isBodyRequest(method: string): boolean {
    let bodyMethods = ['post', 'put']
    return _.includes(bodyMethods, method)
  }

  protected isTextResponseType(url) {
    return _.endsWith(url, 'csv')
  }

  protected filterParams(ri: iApiResourceIdentifier) {
    let filter = this.getFilter(ri)

    if(filter) {
      return {
        filter: this.sanitizedParams(filter)
      }
    }
    else {
      return {}
    }
  }

  protected getFilter(ri: iApiResourceIdentifier) {
    return _.get(ri, 'filter')
  }

  protected sanitizedParams(params) {
    let snakeCase = (value, key) => {
      return _.snakeCase(key)
    }
    return _.mapKeys(params, snakeCase)
  }
}
