import * as _ from 'lodash'

import { iPane } from '../interfaces/index'

import { Card } from './card'

export class Pane implements iPane {
  name: string
  iconName: string
  componentClass: any
  active: boolean
  directive: string

  constructor(attributes) {
    this.setAttributes(attributes)
  }

  toCard() {
    let attributes = {
      data: this,
      title: this.name,
      iconName: this.iconName,
    }
    return new Card(attributes)
  }

  private setStatus(status) {
    this.active = status
  }

  private setAttributes(attributes) {
    let setAttribute = (value, key) => {
      this[key] = value
    }

    _.map(attributes, _.bind(setAttribute, this))
  }
}

