import * as _ from 'lodash'

import { Injectable } from '@angular/core'

import { iResponseParser } from '../../../interfaces/index'

@Injectable({
  providedIn: 'root'
})
export class ResponseParser implements iResponseParser { 
  parse(payload: any): any {
    return _.castArray(payload.data).
      concat(this.loadIncluded(payload))
  }

  private loadIncluded(data) {
    let buildIncluded = (payloadData) => {
      return payloadData
    }

    return _.map(data.included, buildIncluded)
  }
}

