import * as _ from 'lodash';

import { Injectable } from '@angular/core';
import {
  MatDialog, MatDialogRef, MAT_DIALOG_DATA
} from '@angular/material';

import {
  DialogComponentType,
  iDialogAction,
  iDialogActionEvent,
  iDialogComponent,
  iDialogConfig,
  iDialogContentComponent,
  iDialogHtmlElement,
  iMatDialogConfig,
} from '../interfaces/index'

@Injectable({
  providedIn: 'root'
})
export class MatDialogAdapterService {
  private adapterConfigProperties: any[] = [
    'width', 'height',
  ]
  private nonDataProperties: any[] = [
    'componentType',
  ]

  constructor(
    private dialog: MatDialog
  ) {}

  open(
    contentElement: iDialogHtmlElement,
    dialogConfig: iDialogConfig,
  ): iDialogComponent {

    let config = this.buildAdapterDialogConfig(dialogConfig)

    let dialogComponentType = dialogConfig.componentType
    let matDialogComponent = this.getMatDialog(dialogComponentType, config)
    let dialogComponent = this.getDialogComponent(matDialogComponent)
    return this.configuredDialogComponent(dialogComponent, contentElement)
  }

  closeAll() {
    this.dialog.closeAll()
  }

  private getMatDialog(
    dialogComponentType: DialogComponentType,
    config
  ): MatDialogRef<any> {
    return this.dialog.open(dialogComponentType, config)
  }

  private getDialogComponent(matDialog: MatDialogRef<any>): iDialogComponent {
    let dialogComponent = <iDialogComponent>matDialog.componentInstance
    this.connectDialogs(matDialog, dialogComponent)
    return dialogComponent
  }

  private connectDialogs(
    matDialog: MatDialogRef<any>,
    dialogComponent: iDialogComponent
  ) {

    /*
    //Gets an observable that is notified when the dialog is finished opening.
    afterOpened

    //Gets an observable that emits when the overlay's backdrop has been clicked.
    backdropClick

    //Gets an observable that is notified when the dialog has started closing.
    beforeClosed

    //Gets an observable that emits when keydown events are targeted on the overlay.
    keydownEvents
    */

    let matDialogEventNames = [
      'afterClosed', 'afterOpened', 'backdropClick',
      'beforeClosed', 'keydownEvents'
    ]

    let attachMatDialogEvent = (eventName) => {
      matDialog[eventName]().subscribe(event => {
        let action = {
          name: eventName,
          payload: dialogComponent,
        }
        dialogComponent.data.actions$.next(action)
      })
    }
    _.forEach(matDialogEventNames, attachMatDialogEvent)

    dialogComponent.data.actions$.subscribe(action => {
      if(action.name == 'close') {
        matDialog.close(action)
      }
    })
  }


  private buildAdapterDialogConfig(
    dialogConfig: iDialogConfig
  ): iMatDialogConfig {

    let dialogComponentData = {
      data: this.buildComponentData(dialogConfig)
    }

    let adapterDialogConfig = _.pick(
      dialogConfig,
      this.adapterConfigProperties
    )

    return _.merge(adapterDialogConfig, dialogComponentData)
  }

  private buildComponentData(dialogConfig: iDialogConfig) {
    return dialogConfig
  }

  private configuredDialogComponent(
    dialogComponent: iDialogComponent, 
    contentElement: iDialogHtmlElement,
  ): iDialogComponent {
    this.addContentComponent(dialogComponent, contentElement)
    return <iDialogComponent>dialogComponent
  }

  private addContentComponent(
    dialogComponent: iDialogComponent, 
    contentElement: iDialogHtmlElement,
  ): void {
    this.showContentComponent(dialogComponent, contentElement)
    this.launchContentComponent(dialogComponent)
  }

  private showContentComponent(
    dialogComponent: iDialogComponent, 
    contentElement: iDialogHtmlElement,
  ): void {
    document
      .getElementById(dialogComponent.contentElementId)
      .appendChild(<any>contentElement)
  }

  private launchContentComponent(
    dialogComponent: iDialogComponent, 
  ): void {
    let action = this.buildLaunchAction(dialogComponent)
    dialogComponent.data.actions$.next(<iDialogAction>action)
  }

  private buildLaunchAction(
    dialogComponent: iDialogComponent, 
  ): iDialogAction {
    return {
      name: 'launch',
      payload: {
        dialogRef: dialogComponent.dialogRef
      }
    }
  }
}
