import * as _ from 'lodash'

import {
  Component, Input,
  EventEmitter,
  Output,
} from '@angular/core'

import { BaseComponent } from '../../base/base.component'

import {
  iNavigationMenuItem,
} from '../../../../providers/interfaces/index'

@Component({
  selector: 'ceo-navigation-nav-item',
  templateUrl: './nav-item.component.html',
  styleUrls: ['./nav-item.component.scss']
})
export class NavItemComponent extends BaseComponent {
  @Input()
  item: iNavigationMenuItem

  @Output()
  itemEvent: EventEmitter<any> = new EventEmitter<any>()

  className() {
    let classes = [this.item.className]

    if(this.item.hasSubmenu()) {
      classes.push('dropdown')
      classes.push('has-submenu')
    }

    return _.join(classes, ' ')
  }

  onItemEvent(event) {
    this.itemEvent.emit(event)
  }
}
