import {
  Component,
  AfterViewChecked,
  TemplateRef,
  ViewContainerRef,
  ViewChild,
} from '@angular/core';

import { BaseComponent } from '../base/base.component'

@Component({
  selector: 'shared-declarables-svg-loader',
  templateUrl: './svg-loader.component.html',
  styleUrls: ['./svg-loader.component.scss']
})
export class SvgLoaderComponent extends BaseComponent implements AfterViewChecked {
  @ViewChild('template', { read: TemplateRef }) _template: TemplateRef<any>;
  @ViewChild('vc', {read: ViewContainerRef}) vc: ViewContainerRef;

  ngAfterViewChecked() {
    const view = this._template.createEmbeddedView({fromContext: 'John'});
    this.vc.insert(view);
  }
}
