// Service to create custom elements.

import * as _ from 'lodash'

import { Injectable } from '@angular/core'

import { NgElement } from '@angular/elements'

import {
  CustomElementName,
} from '../../interfaces/index'

import { Manager } from './manager.service'

@Injectable({
  providedIn: 'root'
})
export class Factory {
  private defaultElement: null

  constructor(
    public manager: Manager,
  ) {}

  build(elementName: CustomElementName, inputs: any): NgElement {
    let element = this.getElement(elementName, inputs)
    this.launchElement(element)
    return element
  }

  //Private methods

  private getElement(elementName: CustomElementName, inputs: any): NgElement {
    if(this.manager.elementExists(elementName)) {
      let element = this.buildElementInstance(elementName)
      return this.configureElementInstance(element, inputs)
    }
    else {
      return this.defaultElement
    }
  }

  private launchElement(element) {
    document
      .getElementById("custom-elements-wrap")
      .appendChild(<any>element)
  }


  private buildElementInstance(elementName: CustomElementName) {
    return document.createElement(elementName) as NgElement 
  }

  private configureElementInstance(element, inputs) {
    let onClosed = () => {
      return document.body.removeChild(element)
    }
    element.addEventListener('closed', onClosed)


    let setInput = (value, prop) => {
      element[prop]  = value
    }
    _.forEach(inputs, setInput)

    return element
  }
}
