import { Injectable } from '@angular/core'

import {
  FormControl as NgFormControl,
  FormGroup as NgFormGroup,
} from '@angular/forms'

import {
  FormMemberType,
  iFactory,
  iFormMember,
  iFormGroup,
  iFormGroupProps,
  iFormMemberFactoryParams,
} from '../interfaces/index'

import {
  iConstructor,
} from '../../../providers/index'

import {
  FormGroup,
  FormWrapper,
} from '../classes/index'

import { NgFormGroupFactory } from './ng-forms/index'

@Injectable({
  providedIn: 'root'
})
export class FormGroupFactory
  implements iFactory<iFormGroup, iFormMemberFactoryParams> {

  type: FormMemberType = 'form-group'

  constructor(
    private ngFormGroupFactory: NgFormGroupFactory,
  ) { }

  build(params: iFormMemberFactoryParams): iFormGroup {
    let formGroup = this.buildFormGroup(params)
    formGroup.ngControl = this.buildNgFormGroup(formGroup)
    return formGroup
  }

  private buildFormGroup(params: iFormMemberFactoryParams): iFormGroup {
    let formMemberCtor = this.resolveFormGroupCtor(params)
    return new formMemberCtor(<iFormGroupProps>params.data)
  }

  private resolveFormGroupCtor(
    params: iFormMemberFactoryParams
  ): iConstructor<iFormGroup> {

    switch(params.type) {
      case "form-group": {
        return FormGroup
      }
      case "form": {
        return FormWrapper
      }
      default: {
        return FormGroup
      }
    }
  }

  private buildNgFormGroup(formGroup: iFormGroup): NgFormGroup {
    return this.ngFormGroupFactory.build(formGroup)
  }
}
