import {
  AbstractControl as NgAbstractControl,
} from '@angular/forms'

import {
  FormMemberType,
  iFormControl,
  iFormItem,
  iFormMemberError,
  iLabelElement,
} from '../interfaces/index'

import { FormMember } from './form-member'

export class FormItem extends FormMember implements iFormItem {
  type: FormMemberType = 'form-item'

  control: iFormControl
  label: iLabelElement

  constructor(props: Partial<iFormItem> = {}) {
    super(props)
  }

  get ngControl(): NgAbstractControl {
    return this.control.ngControl
  }

  set ngControl(ngControl: NgAbstractControl) {
    this.control.ngControl = ngControl
  }

  get showValidations(): boolean {
    return this.control.showValidations
  }

  get errorMessages(): iFormMemberError[] {
    return this.control.errorMessages
  }
}
