declare var $: any

import * as _ from 'lodash'


import {
  Component, Input,
  EventEmitter,
  Output,
} from '@angular/core'

import { BaseComponent } from '../../base/base.component'

import {
  iNavigationMenuItem,
} from '../../../../providers/interfaces/index'

@Component({
  selector: 'ceo-dropdown-custom-content',
  templateUrl: './custom-content.component.html',
  styleUrls: ['./custom-content.component.scss']
})
export class CustomContentComponent extends BaseComponent {
  @Input()
  item: iNavigationMenuItem

  @Output()
  itemEvent: EventEmitter<any> = new EventEmitter<any>()

  onRouteEvent(event: any) {
    event.item = this.item
    this.itemEvent.emit(event)
  }
}
