// https://www.w3schools.com/tags/tag_video.asp

import * as _ from 'lodash'

import { Component, Input, } from '@angular/core'

import {
  iHtmlVideoTagAttributes,
  iHtmlSourceTagAttributes,
} from '../../interfaces/index'

import { BaseComponent } from '../../../../declarables/index'

@Component({
  selector: 'ceo-html-video',
  templateUrl: './html-video.component.html',
  styleUrls: ['./html-video.component.scss']
})
export class HtmlVideoComponent extends BaseComponent {
  @Input() tagAttributes: iHtmlVideoTagAttributes

  get videoSources(): iHtmlSourceTagAttributes[] {
    var sources
    if(_.isString(this.tagAttributes.src)) {
      let source = {
        src: this.tagAttributes.src,
        type: 'video/mp4'
      }
      sources = [source]
    }
    else {
      sources = this.tagAttributes.src
    }
    return _.flattenDeep(sources) as iHtmlSourceTagAttributes[]
  }
}
