import * as _ from 'lodash'

import {
  Component,
  Input,
  Output,
  EventEmitter
} from '@angular/core'

import { BaseComponent } from '../base/base.component'

@Component({
  selector: 'sunray-shared-navbar-toggle',
  templateUrl: './navbar-toggle.component.html',
  styleUrls: ['./navbar-toggle.component.scss']
})
export class NavbarToggleComponent extends BaseComponent {
  private animationClass: string = "hamburger--slider"
  private activeClass: string = "is-active"
  private ngClass: string = ""
  private state: string = 'inactive'

  @Input()
  animation: string

  @Input()
  isActive: boolean = false

  @Output()
  toggleEmitter: EventEmitter<any> = new EventEmitter()

  ngOnInit() {
    this.ngClass = this.getNgClass()
    this.setState()
  }

  private setState() {
    this.state = this.isActive ? 'active' : 'inactive'
  }

  toggle() {
    let oldState = this.state
    this.isActive = !this.isActive
    this.setState()
    this.emitNewState(oldState)

    if(this.animation) {
      this.animationClass = `hamburger--${this.animation}`
    }
  }

  private emitNewState(oldState) {
    let event = {
      fromState: oldState,
      toState: this.state
    }
    this.toggleEmitter.emit(event)
  }

  getNgClass() {
    let ngClasses = [this.animationClass]

    if(this.isActive) {
      ngClasses.push(this.activeClass)
    }

    return _.join(ngClasses, " ")
  }
}
