import { NgModule }     from '@angular/core'
import {
  FormsModule,
  ReactiveFormsModule
} from '@angular/forms'
import { CommonModule }   from '@angular/common'

import { NgbDatepickerModule } from '@ng-bootstrap/ng-bootstrap'

import { NgsRevealModule }      from 'ngx-scrollreveal'


import {
  MatDialogModule,
} from '@angular/material'

const angularMaterialModules: any[] = [
  MatDialogModule,
]

import { SharedDeclarablesModule } from '../../declarables/index'

import { components } from './components/components'
import { services } from './services/services'
import { dialogTokenProviders } from './tokens/index'

@NgModule({
  imports: [
    FormsModule,
    ReactiveFormsModule,
    CommonModule,
    NgbDatepickerModule,
    NgsRevealModule,
    angularMaterialModules,
    SharedDeclarablesModule,
  ],
  declarations: [
    ...components,
  ],
  exports: [
    ...components,
  ],
  entryComponents: [
    ...components,
  ]
})
export class CeoDialogsModule {
  static forRoot() {
    return {
      ngModule: CeoDialogsModule,
      providers: [
        ...services,
        ...dialogTokenProviders,
      ],
    }
  }
}

