// Service to create custom elements.

import * as _ from 'lodash'

import {
  Injectable, Inject,
} from '@angular/core'

import { createCustomElement } from '@angular/elements'

import {
  iCustomElementConfig,
  CustomElementName,
} from '../../interfaces/index'

import {
  CUSTOM_ELEMENTS_CONFIG
} from '../../tokens/index'

@Injectable({
  providedIn: 'root'
})
export class Manager {
  private ngComponentPath: string = 'ngElementStrategy.componentRef.instance'

  constructor(
    @Inject(CUSTOM_ELEMENTS_CONFIG) private _config,
  ) {}

  get config() {
    return _.reduce(this._config, _.merge)
  }

  define(config: iCustomElementConfig) {
    if(!this.elementExists(config.elementName)) {
      let customElement = this.buildCustomElement(this.elementConfig(config))
      this.defineNativeCustomElement(config.elementName, customElement)
    }
  }

 elementExists(elementName: string): boolean {
    return customElements.get(elementName) != null
  }

  isCustomElement(element: any): boolean {
    return this.hasNgComponent(element)
  }

  getNgComponent(element: any) {
    return _.get(element, this.ngComponentPath)
  }

  //Private methods

  private hasNgComponent(element: any): boolean {
    return _.has(element, this.ngComponentPath)
  }

  private defineNativeCustomElement(
    elementName: CustomElementName,
    element: any
  ) {
    customElements.define(elementName, element)
  }

  private buildCustomElement(config) {
    return createCustomElement(config.ctor, config.opts)
  }

  private elementConfig(config) {
    let defaultConfig = _.get(this.config, config.elementName, {})
    return _.merge(defaultConfig, config)
  }
}
