import * as _ from 'lodash'

import {
  AbstractControl as NgAbstractControl,
  FormGroup as NgFormGroup,
} from '@angular/forms'

import {
  FormMemberType,
  iFormMemberMap,
  iFormMember,
  iFormGroup,
} from '../interfaces/index'

import { FormMember } from './form-member'

export class FormGroup extends FormMember implements iFormGroup {
  type: FormMemberType = 'form-group'

  private _members: iFormMemberMap

  constructor(props: Partial<iFormGroup> = {}) {
    super(props)
  }

  get ngControl(): NgAbstractControl {
    return this._ngControl
  }

  get memberNames(): string[] {
    return _.keys(this.members)
  }

  getMember(memberName: string): iFormMember {
    return this.members[memberName]
  }

  get members(): iFormMemberMap {
    return this._members
  }

  set members(members: iFormMemberMap) {
    this._members = members
  }

  set ngControl(ngControl: NgAbstractControl) {
    this._ngControl = ngControl
  }
  
  //Marks the control as touched. A control is touched by focus and blur events
  //that do not change the value.
  markAsTouched() {
    let marker = 'markAsTouched'
    this.applyMarker(marker)
  }

  //Marks the control as dirty. A control becomes dirty when the
  //control's value is changed through the UI
  markAsDirty() {
    let marker = 'markAsDirty'
    this.applyMarker(marker)
  }

  applyMarker(markerName: string) {
    this.ngControl[markerName]()

    let markMember = (member) => {
      member[markerName]()
    }

    _.map(this.members, markMember)
  }

  // Iterator
  // Allows us to use the object in angular directives
  // (i.e. ngFor, etc)
  [Symbol.iterator]() {
    let current = 0
    let members= _.values(this.members)

    return  {
      next: function () {
        let isEmpty = _.isEmpty(members)
        let value = isEmpty ? null : members[current++]
        let done = isEmpty ? true : current > members.length
        return {
          value: value,
          done: done
        }
      }
    }
  }
}
