import * as _ from 'lodash';

import { Observable } from 'rxjs';

import {
  Component,
  Input,
  Output,
  EventEmitter,
} from '@angular/core';

import { BaseComponent } from '../base/base.component'

@Component({
  selector: 'shared-declarables-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss']
})
export class TableComponent extends BaseComponent {
  @Input()
  collection$: Observable<any>;

  @Input()
  configHeader$: Observable<any>;

  @Input()
  itemActions$: Observable<any>;

  @Input()
  collectionActions$: Observable<any>;

  @Input()
  searchAttributes: any = {};

  @Output()
  actionEmitter: EventEmitter<any> = new EventEmitter<any>();

  triggerAction(event_) {
    this.actionEmitter.emit({
      entity: event_.entity,
      action: event_.action
    });
  }
}
