
import * as _ from 'lodash'

import {
  of as observableOf,
  Observable,
} from 'rxjs'

import { Injectable, Inject } from '@angular/core'

import { iPaneFactory, iPane } from '../interfaces/index'

import { Pane }     from '../classes/index'

import { PaneList } from '../tokens/index'

@Injectable({
  providedIn: 'root'
})
export class PaneFactory implements iPaneFactory {
  constructor(
    @Inject(PaneList) public paneList,
  ) {}

  build$(): Observable<iPane[]> {
    let panes = _.map(this.paneList, this.buildPane)
    return observableOf(panes)
  }

  private buildPane(attributes): iPane {
    return new Pane(attributes)
  }
}
