import { Observable, Subject }  from 'rxjs'
import { map }                  from 'rxjs/operators'

import { Injectable }           from '@angular/core'
import { Response }             from "@angular/http"

import { AngularTokenService }   from "angular-token"

@Injectable()
export class AuthService {
  userSignedIn$:Subject<boolean> = new Subject()

  constructor(public authService: AngularTokenService) { }

  validate() {
    this
      .authService.validateToken()
      .subscribe(
        response => this.handleValidationTokenResponse(response),
        error => this.handleValidationError(error)
      )
  }

  signOutUser(): Observable<Response> {
    return this.authService.signOut().pipe(map(
      res => {
        this.userSignedIn$.next(false)
        return res
      }
    ))
  }

  registerUser(signUpData:  {
    login:string,
    password:string,
    passwordConfirmation:string}
  ): Observable<Response>{

    return this.authService.registerAccount(signUpData).pipe(map(
        res => {
          this.userSignedIn$.next(true)
          return res
        }
    ))
  }

  signInUser(signInData: {login:string, password:string}): Observable<Response> {
    return this.authService.signIn(signInData)
      .pipe(map(
        response => {
          this.userSignedIn$.next(true)
          return response
        }
      ))
  }

  userSignedIn(): boolean {
    return this.authService.userSignedIn()
  }

  private handleValidationTokenResponse(response) {
    if(this.successfulLoginResponse(response)) {
      this.userSignedIn$.next(response.json().success)
    }
    else {
      this.userSignedIn$.next(false)
    }
  }

  private handleValidationError(error) {
    //console.log(error)
  }

  private successfulLoginResponse(response) {
    return response.status == 200
  }
}
